<template>
    <section>
        <el-row>
            <el-col :span="20" :offset="2">
                <el-form :model="tableForm" ref="siteUserForm">
                    <el-table
                        :data="tableForm.listData"
                        ref="siteUserListTbl"
                        tooltip-effect="dark"
                        style="width: 100%"
                        v-loading="listLoading"
                        @selection-change="handleSelectionChange"
                    >
                        <!-- <el-table-column type="selection" width="55"> </el-table-column> -->
                        <el-table-column width="55" prop="bchecked">
                            <template slot-scope="scope">
                                <el-form-item>
                                    <el-checkbox
                                        name="bchecked"
                                        v-model="scope.row.bchecked"
                                        :checked="
                                            tableForm.listData[scope.$index]
                                                .bchecked == 1
                                        "
                                        @change="
                                            handleCheckChange(
                                                $event,
                                                scope.$index
                                            )
                                        "
                                    ></el-checkbox> </el-form-item
                            ></template>
                        </el-table-column>
                        <el-table-column label="No" width="100">
                            <template slot-scope="scope">{{
                                scope.$index + 1
                            }}</template>
                        </el-table-column>
                        <el-table-column
                            prop="username"
                            label="用户名"
                            min-width="3"
                        >
                        </el-table-column>
                        <el-table-column
                            prop="rolename"
                            label="角色"
                            min-width="2"
                            :filters="roles"
                            :filter-method="filterHandler"
                        >
                        </el-table-column>
                        <el-table-column
                            prop="company"
                            label="单位"
                            min-width="2"
                            :filters="companys"
                            :filter-method="filterHandler"
                        >
                        </el-table-column>
                        <el-table-column
                            prop="position"
                            label="职位"
                            min-width="2"
                            :filters="positions"
                            :filter-method="filterHandler"
                        >
                        </el-table-column>
                        <el-table-column
                            prop="bstartfog"
                            label="启动雾炮"
                            min-width="1"
                        >
                            <template slot-scope="scope">
                                <el-form-item
                                    :prop="`listData[${scope.$index}].bstartfog`"
                                >
                                    <el-button
                                        type="success"
                                        icon="el-icon-check"
                                        circle
                                        :disabled="scope.row.bchecked != '1'"
                                        v-if="scope.row.bstartfog == '1'"
                                        @click="changeFog(scope.$index)"
                                    ></el-button>
                                    <el-button
                                        icon="el-icon-close"
                                        circle
                                        :disabled="scope.row.bchecked != '1'"
                                        v-else
                                        @click="changeFog(scope.$index)"
                                    ></el-button> </el-form-item
                            ></template>
                        </el-table-column>
                        <el-table-column
                            prop="bwarn"
                            label="接收告警"
                            min-width="1"
                        >
                            <template slot-scope="scope">
                                <el-form-item
                                    :prop="`listData[${scope.$index}].bwarn`"
                                >
                                    <el-button
                                        type="success"
                                        icon="el-icon-check"
                                        circle
                                        :disabled="scope.row.bchecked != '1'"
                                        v-if="scope.row.bwarn == '1'"
                                        @click="changeWarn(scope.$index)"
                                    ></el-button>
                                    <el-button
                                        icon="el-icon-close"
                                        circle
                                        :disabled="scope.row.bchecked != '1'"
                                        v-else
                                        @click="changeWarn(scope.$index)"
                                    ></el-button></el-form-item
                            ></template>
                        </el-table-column>
                        <el-table-column
                            prop="bsms"
                            label="微信+短信"
                            min-width="1"
                        >
                            <template slot-scope="scope">
                                <el-form-item
                                    :prop="`listData[${scope.$index}].bsms`"
                                >
                                    <el-button
                                        type="success"
                                        icon="el-icon-check"
                                        circle
                                        :disabled="scope.row.bchecked != '1'"
                                        v-if="scope.row.bsms == '1'"
                                        @click="changeSms(scope.$index)"
                                    ></el-button>
                                    <el-button
                                        icon="el-icon-close"
                                        circle
                                        :disabled="scope.row.bchecked != '1'"
                                        v-else
                                        @click="changeSms(scope.$index)"
                                    ></el-button></el-form-item
                            ></template>
                        </el-table-column>
                    </el-table>
                    <el-form-item class="form-button mt20 mr10">
                        <el-button @click="closeSiteUser">取消</el-button>
                        <el-button
                            type="primary"
                            @click="saveSiteUser"
                            :loading="saveLoading"
                            >保存</el-button
                        >
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
    </section>
</template>

<script>
import { getSiteUserList, saveSiteUser } from "../../api/api";
// import dicmixin from "../../assets/dic/dicmixin";

export default {
    components: {},
    // mixins: [dicmixin],

    data() {
        return {
            siteUserListData: [],
            multipleSelection: [],
            roles: [],
            companys: [],
            positions: [],
            tableForm: {
                // params: [{ bstartfog: 0 }],
                // formRules: {
                //     bstartfog: [
                //         {
                //             required: true,
                //             message: "请输入成员名称",
                //             trigger: "blur",
                //         },
                //     ],
                // },
            },
            listLoading: false,
            saveLoading: false,
        };
    },
    mounted() {
        this.getSiteUserList(this.$route.params.id);
    },
    methods: {
        getSiteUserList(siteid) {
            this.listLoading = true;
            getSiteUserList({ SITEID: siteid }).then((res) => {
                if (res.success) {
                    this.tableForm.listData = res.data;
                    let itemroles = [];
                    let itemcompanys = [];
                    let itempositions = [];
                    res.data.forEach((item) => {
                        itemroles.push(item.rolename);
                        itemcompanys.push(item.company);
                        itempositions.push(item.position);
                    });

                    this.roles = [...new Set(itemroles)] //去重复
                        .sort((a, b) => {
                            //排序
                            return a.localeCompare(b);
                        })
                        .map((item) => {
                            //转成对象数组
                            return { text: item, value: item };
                        });
                    this.companys = [...new Set(itemcompanys)]
                        .sort((a, b) => {
                            return a.localeCompare(b);
                        })
                        .map((item) => {
                            return { text: item, value: item };
                        });

                    this.positions = [...new Set(itempositions)]
                        .sort((a, b) => {
                            return a.localeCompare(b);
                        })
                        .map((item) => {
                            return { text: item, value: item };
                        });
                    this.listLoading = false;
                    // this.$nextTick(() => {
                    //     // this.tableForm.listData.forEach((row) => {
                    //     //     if (row.bchecked == "1") {
                    //     //         this.$refs.siteUserListTbl.toggleRowSelection(
                    //     //             row,
                    //     //             true
                    //     //         );
                    //     //     }
                    //     // });
                    // });
                } else {
                    this.listLoading = false;
                }
            });
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
            // var checkedUserids = [];
            // checkedUserids = val.map((item) => {
            //     return item.USERID;
            // });
            for (let i = 0; i < this.tableForm.listData.length; i++) {
                if (val.length > 0) {
                    if (
                        this.tableForm.listData[i].USERID == val[val.length - 1]
                    ) {
                        this.$set(this.tableForm.listData[i], "bstartfog", 0);

                        break;
                    }
                }
            }
        },
        filterHandler(value, row, column) {
            const property = column["property"];
            return row[property] === value;
        },
        handleCheckChange(val, index) {
            if (val) {
                //勾上
            } else {
                //去掉钩子
                this.tableForm.listData[index].bstartfog = 0;
                this.tableForm.listData[index].bsms = 0;
                this.tableForm.listData[index].bwarn = 0;
            }
        },
        changeFog(index) {
            let val = this.tableForm.listData[index].bstartfog;
            this.tableForm.listData[index].bstartfog = val == 1 ? 0 : 1;
        },
        changeSms(index) {
            let val = this.tableForm.listData[index].bsms;
            this.tableForm.listData[index].bsms = val == 1 ? 0 : 1;
        },
        changeWarn(index) {
            let val = this.tableForm.listData[index].bwarn;
            this.tableForm.listData[index].bwarn = val == 1 ? 0 : 1;
        },
        saveSiteUser() {
            this.$refs.siteUserForm.validate((valid) => {
                if (valid) {
                    this.saveLoading = true;
                    let para = {};
                    const keys = [
                        "USERIDS",
                        "bwarns",
                        "bsmss",
                        "bstartfogs",
                        "troubles",
                        "trouble2s",
                        "trouble3s",
                        "pms",
                        "unwashs",
                        "washoffs",
                        "washoff2s",
                        "washoff3s",
                        "specs",
                        "tipovers",
                        "nospec1s",
                        "cable1s",
                        "unloads",
                        "fences",
                        "helmets",
                        "strangers",
                        "trespassers",
                        "fires",
                        "overloads",
                        "cameraoffs",
                        "invades",
                        "soils",
                        "airtights",
                        "vests",
                    ];
                    for (let k = 0; k < keys.length; k++) {
                        para[keys[k]] = [];
                    }
                    for (let i = 0; i < this.tableForm.listData.length; i++) {
                        if (this.tableForm.listData[i].bchecked == 1) {
                            // para.push(this.tableForm.listData[i]);
                            for (let k = 0; k < keys.length; k++) {
                                let newstr = "000";
                                if (
                                    keys[k] == "bsmss" ||
                                    keys[k] == "bwarns" ||
                                    keys[k] == "bstartfogs"
                                ) {
                                    newstr = 0;
                                }
                                para[keys[k]].push(
                                    this.isNull(
                                        this.tableForm.listData[i][
                                            keys[k].substring(
                                                0,
                                                keys[k].length - 1
                                            )
                                        ],
                                        newstr
                                    )
                                );
                            }
                        }
                    }
                    let params = {};
                    params.SITEID = this.$route.params.id;
                    for (let k = 0; k < keys.length; k++) {
                        params[keys[k]] = para[keys[k]].join(",");
                    }

                    saveSiteUser(params).then((res) => {
                        if (res.success && res.data > 0) {
                            this.$message({
                                message: "保存成功",
                                type: "success",
                            });
                            //关闭tab后跳转
                            // let tags = JSON.parse(
                            //     sessionStorage.getItem("Tags")
                            // ).filter((item) => {
                            //     return item.path != this.$route.path;
                            // });

                            // this.$store.commit(
                            //     "saveTagsData",
                            //     JSON.stringify(tags)
                            // );

                            this.closeSiteUser();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                        this.saveLoading = false;
                    });
                }
            });
        },
        closeSiteUser() {
            this.$router.replace(`/site/sites`);
        },
        isNull(val, result) {
            let newstr = result;
            if (val) {
                newstr = val;
            }

            return newstr;
        },
    },
};
</script>
<style  scoped>
.el-form-item {
    margin-bottom: 0;
}
</style>


